html, body {
    height:100%;
}

html {
    display:table;
    width:100%;
}

body {
    display:table-cell;
    text-align:center;
    vertical-align:middle;
}

button {
    border-color: #8e44ad;
    border-radius: 0;
    color: #8e44ad;
    position: relative;
    overflow: hidden;
    z-index: 1;
    -webkit-transition: color 150ms ease-in-out;
    transition: color 150ms ease-in-out;
    margin: 0 10px
}

button:after {
    content: '';
    position: absolute;
    display: block;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 0;
    height: 100%;
    background: #8e44ad;
    z-index: -1;
    -webkit-transition: width 150ms ease-in-out;
    transition: width 150ms ease-in-out;
}

button:hover {
    color: #fff;
}

button:hover:after {
    width: 110%;
}


input{
    width: 200px;
    padding: 10px;
    border: solid 1px #CCC;
    margin: 3px;
}